import React, { Component } from 'react'
import CardGroup from 'react-bootstrap/CardGroup'
import Card from 'react-bootstrap/Card'
import '../css/about.css'
import 'bootstrap/dist/css/bootstrap.css'

let aboutText = "Although I am a software engineer, I very much prefer to consider myself a critical thinker. I work on projects to help out friends, push my skills and abilities, expand my horizons, and have a lot of fun. As I have encountered different problems, I have had to pick up new languages, learn new technologies, and study everything from data structures to algorithms to quantum mechanics. My experience has given me skills in, but not limited to these."
let skillsCards = [
    {
        header: "Backend Skills",
        list: [
            "C++",
            "Python",
            "SQL",
            "AWS Lambda",
            "Google Firebase",
            "Haskell"
        ]
    },
    {
        header: "Frontend Skills",
        list:[
            "React.js",
            "Bootstrap",
            "HTML5 & CSS3",
            "Javascript (ECMAScript 6)"
        ]
    },
    {
        header: "Data Science",
        list: [
            "Pandas",
            "Numpy",
            "Scikit Learn"
        ]
    }
]

class About extends Component{
    render(){
        return(
            <div className="aboutContainer">
                <div className="aboutTextContainer">
                    {aboutText}
                </div>
                <CardGroup className="skillsCardsContainer">
                    {skillsCards.map((sk) =>
                            <Card className="skillsCard">
                                <Card.Header>{sk.header}</Card.Header>
                                <Card.Body>
                                    {sk.list.join(', ')}
                                </Card.Body>
                            </Card>
                    )}
                </CardGroup>
            </div>
        )
    }
}

export default About;