import React, { Component } from 'react'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import 'bootstrap/dist/css/bootstrap.css'

class MyNav extends Component{
    render(){
        return(
            <Navbar>
                <Navbar.Brand href="#home">AG</Navbar.Brand>
                <Nav className="justify-content-end" >
                    <Nav.Item>
                        <Nav.Link onClick={() => document.getElementById('about_id').scrollIntoView({behavior: "smooth"})}>
                            About
                        </Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link onClick={() => document.getElementById('work_id').scrollIntoView({behavior: "smooth"})}>
                            Work
                        </Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link onClick={() => document.getElementById('project_id').scrollIntoView({behavior: "smooth"})}>
                            Projects
                        </Nav.Link>
                    </Nav.Item>

                    <Nav.Item>
                        <Nav.Link onClick={() => document.getElementById('contact_id').scrollIntoView({behavior: "smooth"})}>
                            Contact
                        </Nav.Link>
                    </Nav.Item>

                </Nav>
            </Navbar>
        )
    }
}

export default MyNav;